var ItemControl = /** @class */ (function () {
    function ItemControl(id) {
        this._items = [];
        this._id = id;
    }
    ItemControl.prototype.add = function (item) {
        this._items.push(item);
        this.render();
    };
    ItemControl.prototype.remove = function (item) {
        this._items.splice(this._items.indexOf(item), 1);
        this.render();
    };
    ItemControl.prototype.getAll = function () {
        return this._items.slice();
    };
    ItemControl.prototype.render = function () {
        var itemsElem = document.querySelector(this._id);
        itemsElem.innerHTML = "";
        this.getAll().forEach(function (item) {
            var liElem = document.createElement("li");
            liElem.textContent = item.getName();
            itemsElem.append(liElem);
        });
    };
    return ItemControl;
}());
export default ItemControl;
